<style lang="scss" scoped>
.circle-3 {
  width: 10rem;
}
</style>

<script>
import Cookies from 'js-cookie';
import CircleThree from '@/components/helpers/focus/CircleThree.vue';
import { apiGetOrder } from '@/assets/javascript/api';

export default {
  data() {
    return {
      select: 0,
      CircleNum: 0,
      orderList: [],
    };
  },
  components: {
    CircleThree,
  },
  methods: {
    testQQ(id) {
      apiGetOrder(id.replace(/%20/g, ' '))
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getCookie() {
      if (Cookies.get('luluBakeOrder')) {
        this.orderList = [];
        const tempCookie = Cookies.get('luluBakeOrder').split(';');
        Object.values(tempCookie).forEach((item) => {
          this.orderList.push(item);
        });
        this.orderList.forEach((item) => {
          item.replace(/%20/g, ' ');
        });
      }
    },
  },
  mounted() {
    this.getCookie();
  },
};
</script>
